body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: #E4D8D2;
}

.App {
  display: flex;
  height: 100%;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden;
  text-align: center;
  width: 100vw;
}

.video-wrapper {
  margin: auto;
  position: relative;
}

.cinemagraph {
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
}

#next-wrapper {
  position: absolute;
  margin-left: -80px;
  margin-top: -80px;
  height: 160px;
  width: 160px;
}

#next {
  -webkit-animation: pulsate-tap-point 4s infinite both ease-in-out;
          animation: pulsate-tap-point 4s infinite both ease-in-out;
  border-radius: 10px;
  background-color: white;
  height: 10px;
  left: 50%;
  position: relative;
  top: 50%;
  width: 10px;
}

@-webkit-keyframes pulsate-tap-point {
  0%, 100% {
    box-shadow: 0px 0px 40px 40px white;

  }

  50% {
    box-shadow: 0px 0px 80px 80px white;
  }
}

@keyframes pulsate-tap-point {
  0%, 100% {
    box-shadow: 0px 0px 40px 40px white;

  }

  50% {
    box-shadow: 0px 0px 80px 80px white;
  }
}
